/**
 * General AJAX request handler
 *
 * @package
 * @since 4.0.0
 * @author YITH
 */

const AjaxRequest = {
	xhr: false,

	block( wrap ) {
		if ( wrap && wrap.length && typeof jQuery.fn.block !== 'undefined' ) {
			wrap.addClass( 'ajax-blocked' );
			wrap.block( {
				message: null,
				overlayCSS: {
					background: '#fff no-repeat center',
					opacity: 0.5,
					cursor: 'none',
				},
			} );
		}
	},

	unblock( wrap ) {
		if ( wrap && wrap.hasClass( 'ajax-blocked' ) && typeof jQuery.fn.block !== 'undefined' ) {
			wrap.unblock();
			wrap.removeClass( 'ajax-blocked' );
		}
	},

	call( data, wrap, type ) {
		let self = this;

		type = typeof type !== 'undefined' ? type : 'GET';

		if ( Array.isArray( data ) ) {
			data.push({ name: 'action', value: yith_wcmv_ajax.ajaxAction });
			data.push({ name: 'security', value: yith_wcmv_ajax.ajaxNonce });
			data.push({ name: 'context', value: 'admin' });
		} else {
			data.action = yith_wcmv_ajax.ajaxAction;
			data.security = yith_wcmv_ajax.ajaxNonce;
			data.context = 'admin';
		}

		self.block( wrap );

		self.xhr = jQuery
			.ajax( {
				url: yith_wcmv_ajax.ajaxUrl,
				data,
				type,
			} )
			.fail( ( response ) => {
				console.log( response );
				self.unblock( wrap );
			} )
			.done( ( response ) => {
				self.unblock( wrap );
				self.xhr = false;
			} );

		return self.xhr;
	},

	get( data, wrap ) {
		return this.call( data, wrap, 'GET' );
	},

	post( data, wrap ) {
		return this.call( data, wrap, 'POST' );
	},

	abort() {
		if ( this.xhr ) {
			this.xhr.abort();
		}
	},
};

export default AjaxRequest;
